.main-header {
  background-color: $basic-bg;

  @media (min-width: $tablet-width) {
    padding-left: 30px;
    padding-right: 34px;
    padding-top: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 33px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    padding-left: 0;
    padding-right: 0;
    justify-content: space-between;
    align-items: center;
    gap: 33px;
    margin: 0 auto;
  }
}

.page__header {
  @media (min-width: $tablet-width) {
    width: 768px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    margin: 0 auto;
  }
}

.main-header__wrapper {
  display: flex;
  padding: 14px 20px;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $tablet-width) {
    padding: 0;
  }
}

.main-header__logo {
  width: 33px;
  height: 38px;

  @media (min-width: $desktop-width) {
    padding-left: 0;
    width: 70px;
    height: 59px;
  }
}

.main-header__name {
  width: 101px;
  height: 18px;

  @media (min-width: $tablet-width) {
    padding-left: 13px;
  }

  @media (min-width: $desktop-width) {
    width: 119px;
    height: 21px;
    padding-left: 14px;
  }
}

.main-header__toggle {
  width: 24px;
  height: 24px;
  border: none;
  background-image: url("../img/icons/header-menu.svg");
  background-repeat: no-repeat;
  background-color: transparent;

  @media (min-width: $tablet-width) {
    display: none;
  }
}
// main-header--closed кнопка закрытия меню
.main-header--closed {
  display: none;
  width: 24px;
  height: 24px;
  border: none;
  background-image: url("../img/icons/header-menu--closed.svg");
  background-repeat: no-repeat;
  background-color: transparent;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

