.present {
  @media (min-width: $desktop-width) {
    display: flex;
    background-color: $accent-color;
  }
}

.additional-products--present {
  width: 280px;
  height: 290px;
  background-color: $accent-color;
  margin: 35px auto;

  @media (min-width: $tablet-width) {
    width: 707px;
    height: 200px;
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    min-height: 288px;
    margin-top: 105px;
  }
}

.present__text::before {
  background-image: url("../img/icons/present-icon.svg");
  content: "";
  display: block;
  width: 78px;
  height: 80px;
  margin-bottom: 48px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $tablet-width) {
    width: 80px;
    height: 82px;
    margin-left: -340px;
  }
}

.present__text {
  font-family: $basic-font;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  color: $basic-bg;
  margin: 0 auto;
  padding-top: 55px;
  width: 161px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
    margin: 0;
    margin-left: 421px;
  }

  @media (min-width: $desktop-width) {
    width: 245px;
    padding-top: 185px;
    padding-bottom: 43px;
  }
}
